import * as PIXI from "pixi.js";
import * as Tween from "gsap";

export class StartSlotBtn extends PIXI.Container {

    constructor() {
        super();

        this.onClick = null;

        this.once("added", this.onAdded);
    }

    onAdded() {
        let startBtn = PIXI.Sprite.from("UI_05.png");
        startBtn.anchor.set(0.5);
        this.addChild(startBtn);

        this.circle = PIXI.Sprite.from("UI_12.png");
        this.circle.anchor.set(0.5);
        this.addChild(this.circle);

        startBtn.interactive = true;
        startBtn.buttonMode = true;
        startBtn.on("click", () => this.onClick_Start());

        this.pivot.set(this.width * 0.5, this.height * 0.5);
        this.position.set(this.parent.StageWidth * 0.94, this.parent.StageHeight * 0.87);


        this.circltween = new Tween.TweenMax(this.circle,
            {
                duration: 1,
                rotation: 2 * Math.PI,
                repeat: -1,
                ease: Tween.Sine.easeInOut,
                yoyo: true,
                yoyoEase: Tween.Sine.easeInOut,
            });
    }

    onClick_Start() {

        if (this.onClick != null) {
            this.onClick();
        }
    }


    play() {
        this.circle.visible = true;
        this.circltween.paused = false;
    }

    stop() {
        this.circle.visible = false;
        this.circltween.paused = true;
    }
}