import * as PIXI from "pixi.js";
import pixisound from "pixi-sound";
import { ApplicationMain } from "./MVC/App/ApplicationMain";
import { ApplicationFacade } from "./MVC/App/ApplicationFacade";
import { AudioCommandEvent } from "./MVC/Event/AudioEvent";
import { AssetsManifest } from "./Config/AssetsManifest";
import { AudioManifest } from "./Config/AudioManifest";
import { Tool } from "./Tools/Tool";
import { GamePanel } from "./MVC/View/Panel/GamePanel";

const MultitonKey = "Game";
const StageWidth = 1560;
const StageHeight = 960;

export default class {
    /**
     * @param {Element} root
     * @param {{ isMuteAll:boolean,onMuteAll:Function}} data
     */
    constructor(root, data) {
        this._root = root;
        this.rendererResize = this.rendererResize.bind(this)

        this._data = data;

        this._application = new PIXI.Application(StageWidth, StageHeight);//, { backgroundColor: 0x1099bb }
        this._root.appendChild(this._application.view);

        this.rendererResize();
        window.addEventListener("resize", this.rendererResize);
        window.addEventListener('deviceOrientation', this.rendererResize);

        this.loaderAssets();
    }

    loaderAssets() {

        this.loader = new PIXI.Loader();

        let progress = new PIXI.Text("Loading   0%",
            {
                fontFamily: 'Arial',
                fontSize: 36,
                // fontStyle: 'italic',
                fontWeight: 'bold',
                fill: ['#ffffff', '#ffff99'], // gradient
                stroke: '#4a1850',
                strokeThickness: 5,
                dropShadow: true,
                dropShadowColor: '#000000',
                dropShadowBlur: 4,
                dropShadowAngle: Math.PI / 6,
                dropShadowDistance: 6,
                wordWrap: true,
                wordWrapWidth: 440
            });
        progress.anchor.set(0.5);
        progress.position.set(StageWidth * 0.5, StageHeight * 0.5);
        this._application.stage.addChild(progress);

        let assetkeys = Object.keys(AssetsManifest);
        for (let i = 0; i < assetkeys.length; i++) {
            this.loader.add(assetkeys[i], this._data.assetRootSrc + AssetsManifest[assetkeys[i]]);
        }

        let audiokeys = Object.keys(AudioManifest);
        for (let i = 0; i < audiokeys.length; i++) {
            this.loader.add(audiokeys[i], this._data.assetRootSrc + AudioManifest[audiokeys[i]]);
        }

        this.loader.onProgress.add((loader) => {
            progress.text = "Loading   " + Math.round(loader.progress) + "%";
        });

        this.loader.onComplete.add(() => {
            this._application.stage.removeChild(progress);
            this.createScene();
        });

        this.loader.onError.add((loader) => {
            console.error("Assets Loader Err " + loader);
        });

        this.loader.load();
    }

    createScene() {
        this._app = new ApplicationMain();
        this._app.setData(StageWidth, StageHeight, MultitonKey);
        this._application.stage.addChild(this._app);

        ApplicationFacade.getInstance(MultitonKey).SetMainViwe(this._app);

        ApplicationFacade.getInstance(MultitonKey).sendNotification(AudioCommandEvent.INIT,
            {
                loader: this.loader,
                isMuteAll: this._data.isMuteAll,
                onMuteAll: this._data.onMuteAll
            });

        // ApplicationFacade.getInstance(MultitonKey).sendNotification(GameCommandEvent.INIT,
        //     {
        //         loader: this.loader,
        //         assetRootSrc: this._data.assetRootSrc,
        //         isMuteAll: this._data.isMuteAll,
        //         onReady: this._data.onReady,
        //         onStartGame: this._data.onStartGame,
        //         onFinshGame: this._data.onFinshGame
        //     });

        // let logo = PIXI.Sprite.from("logo");
        // logo.anchor.set(0.5);
        // logo.position.set(StageWidth * 0.5, StageHeight * 0.5);
        // this._app.addChild(logo);

        let gamePanel = new GamePanel({ key: MultitonKey, width: StageWidth, height: StageHeight });
        this._app.addChild(gamePanel);



        ApplicationFacade.getInstance(MultitonKey).sendNotification(AudioCommandEvent.INIT,
            {
                loader: this.loader,
                isMuteAll: this._data.isMuteAll,
                onMuteAll: this._data.onMuteAll
            });

        this._data = null;
    }

    removeScene() {
        try {
            window.removeEventListener('resize', this.rendererResize)
            window.removeEventListener('deviceOrientation', this.rendererResize);
            this._root.removeChild(this._application.view);

        } catch (e) {
            console.warn(e);
        }
        try {
            ApplicationFacade.Destroy(MultitonKey);

            let keys = Object.keys(this.loader.resources);
            keys.forEach(key => {
                switch (this.loader.resources[key].extension) {
                    case 'json':
                        if (this.loader.resources[key].textures !== undefined) {
                            let texturekeys = Object.keys(this.loader.resources[key].textures);
                            texturekeys.forEach(texturekey => {
                                PIXI.Texture.removeFromCache(this.loader.resources[key].textures[texturekey]);
                            });

                            PIXI.BaseTexture.removeFromCache(this.loader.resources[key]);

                        }
                        break;
                    case 'png':
                        PIXI.Texture.removeFromCache(this.loader.resources[key].texture);
                        PIXI.BaseTexture.removeFromCache(this.loader.resources[key].name);
                        PIXI.BaseTexture.removeFromCache(this.loader.resources[key].url);
                        break;
                    case 'mp3':
                        break;
                }
            });

            let assetkeys = Object.keys(AssetsManifest);
            for (let i = 0; i < assetkeys.length; i++) {
                PIXI.BaseTexture.removeFromCache(assetkeys[i]);
            }

            let audiokeys = Object.keys(AudioManifest);
            for (let i = 0; i < audiokeys.length; i++) {
                pixisound.remove(audiokeys[i]);
            }

            this.loader.reset();

            this.loader.onComplete.detachAll();
            this.loader.onLoad.detachAll();
            this.loader.onProgress.detachAll();
            this.loader.onStart.detachAll();
            this.loader.onError.detachAll();

            this._application.destroy()
        } catch (e) {
            console.warn(e);
        }

        this._application = null;
    }

    resetGame() {
        ApplicationFacade.getInstance(MultitonKey).sendNotification(GameCommandEvent.RESET)
    }

    rendererResize() {
        // if (!(this._root && this._root.parentNode)) return false;
        // let { clientWidth, clientHeight } = this.GameCanvas.parentNode;
        let stageSize = this.calculateStageSize(window.innerWidth, window.innerHeight, StageWidth, StageHeight)
        // let stageSize = this.calculateStageSize(clientWidth, clientHeight, StageWidth,StageHeight)
        let stageWidth = stageSize.stageWidth;
        let stageHeight = stageSize.stageHeight;
        let displayWidth = stageSize.displayWidth;
        let displayHeight = stageSize.displayHeight;

        if (this._application.view.width !== stageWidth) {
            this._application.view.width = stageWidth;
        }

        if (this._application.view.height !== stageHeight) {
            this._application.view.height = stageHeight;
        }

        let scalex = displayWidth / stageWidth, scaley = displayHeight / stageHeight;

        this._application.stage.scale.set(scalex, scaley);
        this._application.renderer.resize(displayWidth, displayHeight);
    }

    calculateStageSize(screenWidth, screenHeight, contentWidth, contentHeight) {
        let displayWidth = screenWidth;
        let displayHeight = screenHeight;
        let stageWidth = contentWidth;
        let stageHeight = contentHeight;


        let scaleX = (screenWidth / stageWidth) || 0;
        let scaleY = (screenHeight / stageHeight) || 0;

        /*
            switch (scaleMode) {
                    case egret.StageScaleMode.EXACT_FIT:
                        break;
                    case egret.StageScaleMode.FIXED_HEIGHT:
                        stageWidth = Math.round(screenWidth / scaleY);
                        break;
                    case egret.StageScaleMode.FIXED_WIDTH:
                        stageHeight = Math.round(screenHeight / scaleX);
                        break;
                    case egret.StageScaleMode.NO_BORDER:
                        if (scaleX > scaleY) {
                            displayHeight = Math.round(stageHeight * scaleX);
                        }
                        else {
                            displayWidth = Math.round(stageWidth * scaleY);
                        }
                        break;
                    case egret.StageScaleMode.SHOW_ALL:
                        if (scaleX > scaleY) {
                            displayWidth = Math.round(stageWidth * scaleY);
                        }
                        else {
                            displayHeight = Math.round(stageHeight * scaleX);
                        }
                        break;
                    case egret.StageScaleMode.FIXED_NARROW:
                        if (scaleX > scaleY) {
                            stageWidth = Math.round(screenWidth / scaleY);
                        }
                        else {
                            stageHeight = Math.round(screenHeight / scaleX);
                        }
                        break;
                    case egret.StageScaleMode.FIXED_WIDE:
                        if (scaleX > scaleY) {
                            stageHeight = Math.round(screenHeight / scaleX);
                        }
                        else {
                            stageWidth = Math.round(screenWidth / scaleY);
                        }
                        break;
                    default:
                        stageWidth = screenWidth;
                        stageHeight = screenHeight;
                        break;
        */

        if (scaleX > scaleY) {
            displayWidth = Math.round(stageWidth * scaleY);
        }
        else {
            displayHeight = Math.round(stageHeight * scaleX);
        }

        if (stageWidth % 2 !== 0) {
            stageWidth += 1;
        }
        if (stageHeight % 2 !== 0) {
            stageHeight += 1;
        }
        if (displayWidth % 2 !== 0) {
            displayWidth += 1;
        }
        if (displayHeight % 2 !== 0) {
            displayHeight += 1;
        }

        return {
            stageWidth: stageWidth,
            stageHeight: stageHeight,
            displayWidth: displayWidth,
            displayHeight: displayHeight
        };
    }

    /**
    * 
    * @param {Function} fn
    */
    setOnMuteAll(fn) {
        if (ApplicationFacade.hasCore(MultitonKey)) {
            ApplicationFacade.getInstance(MultitonKey).sendNotification(AudioCommandEvent.ONMUTEALL, fn);
            this._tempOnMuteAll = null;
        }
        else {
            this._tempOnMuteAll = fn;
        }
    }

    /**
    * 
    * @param {Function} fn
    */
    setReadyCallBack(fn) {
        if (ApplicationFacade.hasCore(MultitonKey)) {
            ApplicationFacade.getInstance(MultitonKey).sendNotification(GameCommandEvent.SETREADYCALLBACK, fn);
            this._tempReadyCallBack = null;
        }
        // else
        // {
        //     this._tempReadyCallBack = fn;
        // }
    }
}
