import * as puremvc from "puremvc";
import { ApplicationFacadeEvent } from "../Event/ApplicationFacadeEvent";

export class ApplicationMediator extends puremvc.Mediator
{
    static get Name ()
    {
        return "ApplicationMediator";
    }

    constructor(viewComponent)
    {
        super(ApplicationMediator.Name, viewComponent);
    }

    listNotificationInterests ()
    {
        return [
            ApplicationFacadeEvent.LOADPANEL];
    }

    handleNotification (notification)
    {
        switch (notification.getName())
        {
            case ApplicationFacadeEvent.LOADPANEL:
                this.viewComponent.loadPanel(notification.getBody());
                break;
            default:
                break;
        }
    }
}

