import * as PIXI from "pixi.js";
import { SymbolGroup } from "./SymbolGroup";
import * as Tween from "gsap";

export class SlotGroup extends PIXI.Container {

    constructor() {
        super();

        this.symbolTextures =
        {
            0: {
                normal: PIXI.Texture.from("sp_08.png"),
                bular: PIXI.Texture.from("sp_11.png")
            },
            1: {
                normal: PIXI.Texture.from("sp_05.png"),
                bular: PIXI.Texture.from("sp_04.png")
            },
            2: {
                normal: PIXI.Texture.from("sp_10.png"),
                bular: PIXI.Texture.from("sp_03.png")
            },
            3: {
                normal: PIXI.Texture.from("sp_07.png"),
                bular: PIXI.Texture.from("sp_06.png")
            },
            4: {
                normal: PIXI.Texture.from("sp_01.png"),
                bular: PIXI.Texture.from("sp_14.png")
            },
            5: {
                normal: PIXI.Texture.from("sp_13.png"),
                bular: PIXI.Texture.from("sp_12.png")
            },
            6: {
                normal: PIXI.Texture.from("sp_16.png"),
                bular: PIXI.Texture.from("sp_24.png")
            },
            7: {
                normal: PIXI.Texture.from("sp_15.png"),
                bular: PIXI.Texture.from("sp_02.png")
            },
            8: {
                normal: PIXI.Texture.from("sp_18.png"),
                bular: PIXI.Texture.from("sp_17.png")
            },
            9: {
                normal: PIXI.Texture.from("sp_19.png"),
                bular: PIXI.Texture.from("sp_20.png")
            },
            10: {
                normal: PIXI.Texture.from("sp_25.png"),
                bular: PIXI.Texture.from("sp_21.png")
            }
        };


        this.once("added", this.onAdded);

    }

    onAdded() {


        this.pivot.set(0.5);
        this.position.set(this.parent.StageWidth * 0.5, this.parent.StageHeight * 0.5);

        // let timeline = new Tween.TimelineMax({ paused: true });

        this.sysmbolgroups = [];
        for (let x = 0; x < 5; x++) {
            let temp = new SymbolGroup(1000, x * 250, x == 4 ? () => this.parent.onCompleteSlot() : null);
            temp.position.x = -350 + x * 200;
            temp.position.y = -220;
            this.addChild(temp);
            this.sysmbolgroups.push(temp)
            // let tweenmax = new Tween.TweenMax(temp,
            //     {
            //         moveY: 3000,
            //         onUpdate: () => {
            //             console.log(this.sysmbolgroups[x].moveY)
            //         },
            //         ease: Tween.Sine.easeInOut,
            //         delay: 2,
            //         duration: 5
            //     });

            // timeline.add(tweenmax);
        }

        // timeline.play();
    }

    startSlot() {
        for (let x = 0; x < 5; x++) {
            this.sysmbolgroups[x].startSlot();
        }
    }

    reset() {
        for (let x = 0; x < 5; x++) {
            this.sysmbolgroups[x].reset();
        }
    }

    tick(delta) {
        for (let x = 0; x < 5; x++) {
            this.sysmbolgroups[x].tick(delta);
        }
    }


    getRandomTexture() {
        let rand = Math.floor(Math.random() * Object.keys(this.symbolTextures).length);
        return this.symbolTextures[rand];
    }

}