import * as PIXI from "pixi.js";
import { SlotGroup } from "../Component/SlotGroup";
import { StartSlotBtn } from "../Component/StartSlotBtn";
import * as Tween from "gsap";


export class GamePanel extends PIXI.Container {

    constructor(data) {
        super();

        this._key = data.key;
        this._stageWidth = data.width;
        this._stageHeight = data.height;


        this.isStartSlot = false;
        this.slotTime = 2000;//ms

        this.ticker = new PIXI.Ticker();
        this.ticker.add((d) => this.update(d));

        this.once("added", () => this.onAdded());
    }

    onAdded() {
        // ApplicationFacade.getInstance(this._key).registerMediator(new TempMediator(this));

        let bg = new PIXI.NineSlicePlane(PIXI.Texture.from("bg"), 10, 10, 10, 10);
        this.addChild(bg);

        let frame = new PIXI.NineSlicePlane(PIXI.Texture.from("frame1"), 10, 10, 10, 10);// PIXI.Sprite.from("frame1");

        frame.scale.set(1);
        frame.position.set(this._stageWidth * 0.48, this._stageHeight * 0.56);
        frame.width *= 1.2;
        frame.pivot.set(frame.width * 0.5, frame.height * 0.5);
        // frame.blendMode = PIXI.BLEND_MODES.DARKEN;
        this.addChild(frame);

        this.slotGroup = new SlotGroup();
        this.addChild(this.slotGroup);

        this._slotMask = new PIXI.Graphics();
        this._slotMask.beginFill(0xffffff, 1);
        this._slotMask.drawRect(0, 0, 1100, 650);
        this._slotMask.endFill();
        this._slotMask.pivot.set(this._slotMask.width * 0.5, this._slotMask.height * 0.5);
        this._slotMask.position.set(this._stageWidth * 0.52, this._stageHeight * 0.525);
        this.addChild(this._slotMask);

        // let frameMask = PIXI.Sprite.from("frameMask");
        // frameMask.anchor.set(0.5);
        // frameMask.width *= 1.2;
        // frameMask.position.set(this._stageWidth * 0.48, this._stageHeight * 0.56);
        // this.addChild(frameMask)
        // // frameMask.visible = false;

        this.slotGroup.mask = this._slotMask;

        this.startSlotBtn = new StartSlotBtn();
        this.addChild(this.startSlotBtn);
        this.startSlotBtn.onClick = () => {

            if (!this.isStartSlot) {

                this.isStartSlot = true;
                this.startSlotBtn.stop();
                this.slotGroup.startSlot();
                this.ticker.start()
            }
        };


    }

    update(delta) {
        // console.log((this.ticker.deltaMS / 1000))
        if (this.isStartSlot) {
            this.slotGroup.tick(this.ticker.deltaMS / 1000);
        }
    }


    onCompleteSlot() {
        this.isStartSlot = false;
        this.ticker.stop();
        this.slotGroup.reset();
        this.startSlotBtn.play();
    }


    get StageWidth() {
        return this._stageWidth;
    }

    get StageHeight() {
        return this._stageHeight;
    }
}