import * as PIXI from "pixi.js";

export class Symbol extends PIXI.Container {

    init(textureData, y) {
        this.textureData = textureData;
        this.orignY = y;

        this.sprite = new PIXI.Sprite(textureData.normal);
        this.sprite.anchor.set(0.5);
        this.addChild(this.sprite);

        this.turnCount = 0;
        this.position.y = y;
    }

    reset() {
        this.turnCount = 0;
        this.position.y = this.orignY;
    }

    upY(y) {
        // console.log(Math.round(y) + " " + this.orignY);
        // if (Math.round(y) == this.orignY)
        //     this.sprite.texture = this.textureData.normal;
        // else
        //     this.sprite.texture = this.textureData.bular;

        this.position.y = y;
    }

    upTextureData(textureData) {
        this.textureData = textureData;
        this.sprite.texture = this.textureData.normal;
    }

    playEffect() {

    }
}