import * as PIXI from "pixi.js";
import { Symbol } from "./Symbol";

export class SymbolGroup extends PIXI.Container {

    constructor(slotTime, delayTime, slotCompleteCallBack = null) {
        super();

        this.moveY = 0;
        this.SpaceY = 160;

        this.currSlotTime = 0;
        this.slotTime = slotTime;
        this.speed = 1;

        this.currdis = 0;
        this.totaldis = (this.SpaceY * 6) * 3;

        this.delayTime = delayTime;

        this.symbols = [];

        // console.log(this.backout(0.6, 10))

        this.slotCompleteCallBack = slotCompleteCallBack;

        this.once("added", this.onAdded);
    }

    set MoveY(value) {
        this.moveY = value;
    }

    get MoveY() {
        return this.moveY;
    }

    onAdded() {

        this.symbolCount = 6;

        for (let y = 0; y < this.symbolCount; y++) {

            let symbol = new Symbol();
            this.addChild(symbol);
            let posY = y * this.SpaceY;

            if (posY >= (this.symbolCount - 1) * this.SpaceY)
                posY -= this.symbolCount * this.SpaceY;

            symbol.init(this.parent.getRandomTexture(), posY);
            this.symbols.push(symbol);

        }
    }

    startSlot() {

        setTimeout(() => {

            this.slot = true;
            this.currdis = 0;
            this.currSlotTime = 0;
            this.currDelayTime = 0;
            this.startTime = Date.now();
        }, this.delayTime);
    }

    reset() {
        for (let i = 0; i < this.symbolCount; i++) {
            this.symbols[i].reset();
        }
    }

    tick(delta) {
        let now = Date.now();
        if (this.slot) {

            let phase = (now - this.startTime) / this.slotTime// Math.min(1, );

            if (phase <= 1) {
                this.currSlotTime = this.lerp(this.currSlotTime, this.slotTime, this.easeInOut(phase));
                let posY = phase * this.totaldis;

                for (let y = 0; y < this.symbolCount; y++) {
                    let newY = this.symbols[y].orignY + (posY - this.symbols[y].turnCount * this.SpaceY * this.symbolCount);


                    if (newY > this.SpaceY * (this.symbolCount - 1)) {
                        this.symbols[y].turnCount++;
                        this.symbols[y].upTextureData(this.parent.getRandomTexture());
                        newY -= (this.SpaceY * this.symbolCount);
                    }
                    this.symbols[y].upY(newY);
                }
            }
            else {
                let backphase = Math.min(1, (now - this.startTime - this.slotTime) / 1000);
                if (backphase < 1) {

                    let newY = Math.sin(Math.PI * this.backout(backphase, 0.5)) * 100;
                    console.log()
                    for (let y = 0; y < this.symbolCount; y++) {
                        this.symbols[y].upY(this.symbols[y].orignY + newY);
                    }
                }
                else {
                    if (this.slotCompleteCallBack != null)
                        this.slotCompleteCallBack();

                    this.slot = false;
                }

            }

        }
    }

    // Basic lerp funtion.
    lerp(a1, a2, t) {
        return a1 * (1 - t) + a2 * t;
    }

    easeInOut(t) {
        return t < .5 ? 4 * t * t * t : (t - 1) * (2 * t - 2) * (2 * t - 2) + 1
    }

    backout(t, amount) {
        return (--t * t * ((amount + 1) * t + amount) + 1);
    }
}